.home-container{
  min-height: 83.5vh;
  margin-top: 3.55rem;
  padding-top: 1rem;
}

/* title and subtitle */

.title-box{
  margin: 4rem 0 4rem 0;
}

.title{
  
  font-size: 5rem;
  text-align: center;
  color: #4582ec;
}

.subtitle{
  width: 65%;
  margin: 0 auto;
  text-align: center;
  font-size: 1.1rem;
}

/* simple what section */

.simple-what-box {
  margin-left: 3rem;
  margin-right: 3rem;
  margin-bottom: 6rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 1rem;
  
}

.simple-info {
  background-color: #4582ec;
  padding: 1rem;
  padding-left: 2rem;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
}

/* team section */

.team-title {
  text-align: center;
}

.team-container{
  display: flex;
  justify-content: center;
}

.team-box {
  /* background-color: #4582ec; */
  border: .15rem solid #4582ec;
  min-width: 12rem;
  padding: .5rem;
  border-radius: 1rem;
  margin-bottom: 1.5rem;
}

.team-img-container {
  display: flex;
  justify-content: center;
}

.team-img {
  border-radius: 50%;
}

.team-name {
  font-size: 1.1rem;
  text-align: center;
  color: #4582ec;
  letter-spacing: .08em;
}

.team-email {
  font-size: .9rem;
  text-align: center;
}

/* join section */

.join-container {
  display: flex;
  justify-content: center;
  margin: 3rem 0;
}

.join-box {
  padding: 3rem;
  border-radius: 1rem;
  box-shadow: 0 4px 8px 0 rgba(69, 130, 236, 0.2), 0 6px 20px 0 rgba(69, 130, 236, 0.19);
}

.join-title {
  font-size: 2.126rem;
}

.join-btn {
  width: 40%;
}

@media screen and (min-width: 1200px) {
  .team-box {
    border: none;
  }
}

@media screen and (max-width: 991.98px) {
  
  .simple-what-box {
    margin-left: 1.2rem;
    margin-right: 1.2rem;
  }

  .simple-info {
    padding: 1.5rem;
  }

  .simple-info-title {
    font-size: 1.3rem;
  }
  
  .simple-img {
    display: flex;
    align-items: center;
  }
}

@media screen and (max-width: 767.98px) {
  .simple-what-box {
    margin-left: .1rem;
    margin-right: .1rem;
  }
}

@media screen and (max-width: 575.98px) {
  .title {
    font-size: 2.8rem;
  }

  .subtitle {
    width: 80%;
    font-size: .9rem;
  }
  
  .simple-what-box {
    margin-left: .1rem;
    margin-right: .1rem;
  }

  .simple-info {
    border-top-right-radius: 0;
    border-bottom-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
  }

  .simple-info-title {
    text-align: center;
  }

  .simple-info-desc {
    text-align: justify;
  }

  .team-title {
    font-size: 1.8rem;
  }

  .join-box {
    padding: 1.5rem;
    border-radius: 1rem;
    
  }
}